import React, {Component} from 'react';

class DetailGoods extends Component {
  componentDidMount = () => {
    const {lazyLoading} = this.props;
    lazyLoading();
  };

  render() {
    const {detailGoods} = this.props;
    const {property, pic} = detailGoods;
    const {
      color,
      suit,
      material,
      size,
      band,
      name,
      cat,
      style,
    } = property;

    return (
      <div>
        <div
          className="goods-detail-info"
          style={{
            padding: 1+'vw',
          }}
        >
          <p>: {band}</p>
          <p style={{
            marginTop: 1.4+'vw',
            marginBottom: 1+'vw',
          }}>
            :
          </p>
          <div className="goods-detail-info-list">
            <span>: {color}</span>
            <span>: {suit}</span>
            <span>: {material}</span>
            <span>: {size}</span>
            <span>: {band}</span>
            <span>: {name}</span>
            <span>: {cat}</span>
            <span>: {style}</span>
          </div>
        </div>
        <ul className="goods-detail-img">
          <li>
            <img data-src={require(`../../img/detail/${pic[0]}`)} alt="" />
            <img data-src={require(`../../img/detail/${pic[1]}`)} alt="" />
            <img data-src={require(`../../img/detail/${pic[2]}`)} alt="" />
          </li>
        </ul>
      </div>
    );
  }
}

export {DetailGoods};


