import React, {Component} from 'react';

class Footer extends Component {
  render() {
    const {moreNavToggle} = this.props;
    return (
      <div id="footer-wrap" onClick={moreNavToggle}>
        <footer className="autoWidth">
          <p>  - 2017 NOVATEX Inc.</p>
        </footer>
      </div>
    )
  }
}

export {Footer};


